 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

:root {
	--font-family: "Open Sans", sans-serif;
	--font-p: "Rubik", sans-serif;
	--size-p: 1.4rem;
	--primary: #2f2e2e;
	--primary-dark: #104e61;
	--dark: #161616;
	--backgroung-light: #e9e6e6;
	--background-form: #f7f0da;
	--text-color: #444;
	--border-color-input: #cccccc;
	--text-color-light: #fff;
	--text-color-input: rgb(233, 231, 231);
	--text-color-h1: rgb(237, 158, 6);
	--text-color-label: rgb(20, 118, 168);
	--box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
		rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
	--border: 1px, solid, rgb(83, 81, 81);
	--alert: #e74c3c;
	--font-h1: "Lobster", cursive;
}

.wp-block-create-block-recettes-app {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}

/* Forcer la taille uniforme des images */
.home .image-container {
	width: 100% !important;
	overflow: hidden !important;
	position: relative !important;
	height: 15rem;
	background-size: cover;
	transition: transform 1s;
	border-radius: 25px;
}

.home .image-container img {
	position: absolute !important;
	top: 50% !important;
	left: 50% !important;
	width: 100% !important;
	height: 100% !important;
	transform: translate(
		-50%,
		-50%
	) !important; /* Centrer l'image dans le conteneur */
	object-fit: cover !important; /* Recadrage automatique de l'image pour couvrir tout le conteneur */
	transition: transform 0.3s ease-in-out !important;
	border: 5px solid rgb(92, 92, 95) !important;
}

.home .image-container:hover img {
	transform: translate(-50%, -50%) scale(1.1) !important; /* Zoom au survol tout en restant centr√© */
}

/* Forcer la grille des recettes */
.home .wp-block-query.alignwide .wp-block-post-template {
	display: grid !important;
	grid-template-columns: repeat(3, 1fr) !important;
	gap: var(--wp--preset--spacing--30) !important;
}

.home .cpt-item {
	margin-bottom: var(--wp--preset--spacing--2) !important;
	position: relative !important;
}

.home .cpt-item .wp-block-post-featured-image {
	margin-bottom: var(--wp--preset--spacing--0) !important;
	box-shadow: var(--box-shadow);
}

.home .cpt-item .wp-block-post-title {
	font-size: 2rem !important;
	text-align: center;
	margin-top: var(--wp--preset--spacing--10) !important;
	margin-bottom: var(--wp--preset--spacing--0) !important;
}

.home .cpt-item .wp-block-post-excerpt {
	color: var(--wp--preset--color--contrast-2) !important;
	font-size: var(--wp--preset--font-size--small) !important;
	margin-top: var(--wp--preset--spacing--2) !important;
	text-align: center;
}

.overflow {
	overflow: hidden;
}

.button {
	background-color: #555555;
	font-size: 1.4rem;
	color: wheat;
	padding: 1rem;
	box-shadow: var(--box-shadow);
	text-decoration: none;
} /* Black */
